import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Login.vue";
import Register from "../views/Register.vue";
import Terms from "../views/Terms.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/Login",
    name: "Login",
    component: Login
  },
  {
    path: "/Register",
    name: "Register",
    component: Register
  },
  {
    path: "/Terms",
    name: "Terms",
    component: Terms
  }
];

const router = new VueRouter({
  mode: process.env.NODE_ENV === "GH-PAGES" ? "hash" : "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
